<template>
  <div class="cart">
    <van-nav-bar title="购物车" fixed />
    <div v-if="this.$store.getters.ifToken && cartList.length">
      <!-- 购物车开头 -->
      <div class="cart-title">
        <span class="all"
          >共<i>{{ totalGoods }}</i
          >件商品</span
        >
        <span class="edit" @click="enterDelMode">
          <van-icon name="edit" v-if="!delMode" />
          <span :style="{ color: delMode ? 'red' : '' }">{{
            delMode ? "退出管理" : "管理"
          }}</span>
        </span>
      </div>

      <!-- 购物车列表 -->
      <div class="cart-list">
        <div class="cart-item" v-for="(item, index) in cartList" :key="index">
          <van-checkbox
            :value="item.isChecked"
            @click="toggleChecked(item.goods_id)"
          ></van-checkbox>

            <div class="show" @click="$router.push(`/detail/${item.goods_id}`)">
              <img :src="item.goods.goods_image" alt="" />
            </div>
            <div class="info" @click="$router.push(`/detail/${item.goods_id}`)">
              <span class="tit text-ellipsis-2">{{
                item.goods.goods_name
              }}</span>
              <span class="bottom">
                <div class="price">
                  ¥ <span>{{ item.goods.goods_price_min }}</span>
                </div>
                <CountBox
                  :value="item.goods_num"
                  @input="
                    (num) => changeCount(item.goods_id, num, item.goods_sku_id)
                  "
                ></CountBox>
              </span>
            </div>
        </div>
      </div>

      <div class="footer-fixed">
        <div class="all-check">
          <van-checkbox
            icon-size="18"
            :value="isAllChecked"
            @click="checkAll(isAllChecked)"
          ></van-checkbox>
          全选
        </div>

        <div class="all-total">
          <div class="price">
            <span>合计：</span>
            <span
              >¥ <i class="totalPrice">{{ checkedGoodsPrice }}</i></span
            >
          </div>
          <div
            v-if="!delMode"
            :class="{ toCheckout: true, disabled: checkedGoodsNum === 0 }"
            @click="toCheckout"
          >
            结算{{ checkedGoodsNum ? "（" + checkedGoodsNum + "）" : null }}
          </div>
          <div
            v-else
            :class="{ delete: true, disabled: checkedGoodsNum === 0 }"
            @click="toDelGoods"
          >
            删除
          </div>
        </div>
      </div>
    </div>

    <!-- 购物车为空时显示 -->
    <div class="empty-cart" v-else>
      <img src="@/assets/empty.png" alt="" />

      <div class="tips">购物车空空如也，去逛逛吧</div>
    </div>
  </div>
</template>

<script>
import CountBox from '@/components/CountBox.vue'
import { mapState, mapGetters, mapMutations } from 'vuex'

export default {
  name: 'CartPage',

  components: {
    CountBox
  },

  data () {
    return {
      delMode: false

    }
  },

  computed: {
    ...mapState('cart', ['cartList']),
    ...mapGetters('cart', ['totalGoods', 'checkedGoodsNum',
      'checkedGoodsPrice', 'isAllChecked', 'checkedGoodsIds'])
  },

  methods: {
    ...mapMutations('cart', ['toggleChecked', 'checkAll']),

    changeCount (goodsId, goodsNum, goodsSkuId) {
      const obj = { goodsId, goodsNum, goodsSkuId }
      this.$store.dispatch('cart/changeCount', obj)
    },

    enterDelMode () {
      this.delMode = !this.delMode
    },

    toDelGoods () {
      this.$store.dispatch('cart/toDelGoods')
    },

    toCheckout () {
      if (!this.checkedGoodsIds.length) {
        return
      }
      this.$router.push({
        path: '/checkout',
        query: {
          mode: 'cart',
          cartIds: this.checkedGoodsIds.join(',')
        }
      })
    }
  },

  created () {
    if (this.$store.getters.ifToken) {
      this.$store.dispatch('cart/readCartList')
    }
  }
}
</script>

<style lang="less" scoped>
// 主题 padding
.cart {
  padding-top: 48px;
  padding-bottom: 100px;
  background-color: #f5f5f5;
  min-height: 100vh;
  .cart-title {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    font-size: 14px;
    .all {
      i {
        font-style: normal;
        margin: 0 2px;
        color: #fa2209;
        font-size: 16px;
      }
    }
    .edit {
      .van-icon {
        font-size: 18px;
      }
    }
  }

  .cart-item {
    margin: 0 10px 10px 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    border-radius: 5px;

    .show img {
      width: 100px;
      height: 100px;
    }
    .info {
      width: 210px;
      padding: 10px 5px;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .bottom {
        display: flex;
        justify-content: space-between;
        .price {
          display: flex;
          align-items: flex-end;
          color: #fa2209;
          font-size: 12px;
          span {
            font-size: 16px;
          }
        }
      }
    }
  }
}

.footer-fixed {
  position: fixed;
  left: 0;
  bottom: 50px;
  height: 50px;
  width: 100%;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;

  .all-check {
    display: flex;
    align-items: center;
    .van-checkbox {
      margin-right: 5px;
    }
  }

  .all-total {
    display: flex;
    line-height: 36px;
    .price {
      font-size: 14px;
      margin-right: 10px;
      .totalPrice {
        color: #fa2209;
        font-size: 18px;
        font-style: normal;
      }
    }

    .toCheckout,
    .delete {
      min-width: 100px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      background-color: #fa2f21;
      color: #fff;
      border-radius: 18px;
      &.disabled {
        background-color: #ff9779;
      }
    }
  }
}
.empty-cart {
  padding: 80px 30px;
  img {
    width: 140px;
    height: 92px;
    display: block;
    margin: 0 auto;
  }
  .tips {
    text-align: center;
    color: #666;
    margin: 30px;
  }
  .btn {
    width: 110px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    background-color: #fa2c20;
    border-radius: 16px;
    color: #fff;
    display: block;
    margin: 0 auto;
  }
}
</style>
